import { Space, Tooltip, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import moment from 'moment'
import { Link } from 'react-router-dom'

export const caseCol = ({handleDelete}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'caseTitle',
            key: 'caseTitle',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {moment(record.date).format("DD MM YYYY")}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'place',
            key: 'place',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'related',
            key: 'related',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 100,
            render: (text, record) => (
                <Tooltip placement="top" title={record.village + "," + record.commune + "," + record.district + "," + record.province}>
                    <Space size="middle">
                        {record.village}
                    </Space>
                </Tooltip>
            ),
        },
        {
            title: '',
            dataIndex: 'remark',
            key: 'remark',
            width: 150,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link to={"/subCase/" + record.id}><EditOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}