import React, { useContext,useState,useEffect } from 'react'
import { Modal, Form, Input, Row, Col, Button,Select} from 'antd';
import { QuarantineController } from '../../../context/quarantineContext'
import { ListSelect } from '../../../static/own-comp'
import { provinceData, districtData, communeData, villageData } from '../../../context/headerContext'
import { convertToDistrict, convertToCommune, convertToVillage } from '../../../function/fn'

const { Option } = Select;

export default function AddQuarantine({ open, setOpen }) {
    const { quarantineDataDispatch } = useContext(QuarantineController)

    let [form] = Form.useForm()

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    // useEffect(() => {
    //     // form.setFieldsValue(
    //     //     setEditSubCase(data)
    //     // )
    //     setProvince(data?.province)
    //     setDistrict(data?.district)
    //     setCommune(data?.commune)
    // }, [data])


    const onFinish = (values) => {
        console.log('Success:', values);

        quarantineDataDispatch({ type: 'ADD_QUARANTINE', payload: values })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addUser"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="quarantineName"
                            rules={[{ required: true, message: 'Field is required!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="inCharge"
                            rules={[{ required: true, message: 'Field is required!' }]}
                        >
                            <Input placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11}}>
                        <Form.Item
                            name="place"
                            rules={[{ required: true, message: 'Field is required!' }]}
                        >
                            <Input placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: 'Field is required!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: 'Field is required!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: 'Field is required!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: 'Field is required!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    <Col xs={24}>
                        <Form.Item
                            name="note"
                        // rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
