import React, {useContext, useEffect} from 'react'
import { Modal, Form, Input, DatePicker, Row, Col, Button } from 'antd'
import {CaseController} from '../../../context/caseContext'
import {setEditCase} from '../../../function/set'

export default function EditCase({ open, setOpen, data, setData }) {
    const {caseDataDispatch} = useContext(CaseController)

    let [form] = Form.useForm()

    useEffect(() => {
        if (data !== undefined){
            form.setFieldsValue(setEditCase(data))
        }
    }, [data])

    const onFinish = (values) => {
        console.log('Success:', values);

        caseDataDispatch({type: 'EDIT_CASE', payload: {...values, id: data.id}})
        setData({...values, id: data.id})

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
            destroyOnClose={false} 
            getContainer={false}
            forceRender
        >
            <Form
                form={form}
                name="editCase"
                // initialValues={setEditCase(data)}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{span:11}}>
                        <Form.Item
                            name="caseTitle"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{span:11, offset: 2}}>
                        <Form.Item
                            name="date"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Form.Item
                            name="place"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{span:11}}>
                        <Form.Item
                            name="related"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{span:11, offset: 2}}>
                        <Form.Item
                            name="username"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24}>
                        <Form.Item
                            name="remark"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button 
                            htmlType="submit"
                            type="primary"
                            style={{width: "100%"}}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
